"use client";

import { useEffect, useState, useRef } from "react";
import type { ChangeEvent, RefObject } from "react";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import {
  TrendingUp,
  TrendingDown,
  DollarSign,
  PieChart,
  Target,
  Receipt,
} from "lucide-react";
import Navigation from "@/components/Navigation";
import InteractiveDashboard from "@/components/client/InteractiveDashboard";
import { Button } from "@/components/ui/button";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";
import Tesseract from "tesseract.js";

export default function DashboardPage() {
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();
  const [userName, setUserName] = useState("");
  const [showUploadModal, setShowUploadModal] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [uploadedFile, setUploadedFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string | null>(null);
  const [uploadSuccess, setUploadSuccess] = useState(false);
  const [ocrText, setOcrText] = useState("");
  const [ocrLoading, setOcrLoading] = useState(false);
  const [ocrError, setOcrError] = useState("");
  const [parsedResult, setParsedResult] = useState<{
    amount?: string;
    currency?: string;
    item?: string;
  } | null>(null);
  const [parsedItems, setParsedItems] = useState<any[]>([]);

  // Mock user data - replace with real data from your auth/API
  const user = {
    name: "John Doe",
    monthlyBudget: 2500,
    occupation: "Software Developer",
    financialGoal: "savings",
  };

  // Mock financial data - replace with real API calls
  const [financialData, setFinancialData] = useState({
    totalSpent: 1847.32,
    remainingBudget: 652.68,
    budgetProgress: 73.9,
    monthlyChange: 8.5,
    categories: [
      {
        name: "Food & Dining",
        amount: 567.45,
        percentage: 30.7,
        color: "bg-blue-500",
      },
      {
        name: "Transportation",
        amount: 289.2,
        percentage: 15.7,
        color: "bg-green-500",
      },
      {
        name: "Shopping",
        amount: 456.78,
        percentage: 24.7,
        color: "bg-purple-500",
      },
      {
        name: "Bills & Utilities",
        amount: 334.67,
        percentage: 18.1,
        color: "bg-orange-500",
      },
      {
        name: "Entertainment",
        amount: 199.22,
        percentage: 10.8,
        color: "bg-pink-500",
      },
    ],
    recentTransactions: [
      {
        id: 1,
        description: "Grocery Store",
        amount: 67.45,
        category: "Food & Dining",
        date: "2025-01-15",
        type: "expense" as const,
      },
      {
        id: 2,
        description: "Gas Station",
        amount: 42.3,
        category: "Transportation",
        date: "2025-01-14",
        type: "expense" as const,
      },
      {
        id: 3,
        description: "Netflix",
        amount: 15.99,
        category: "Entertainment",
        date: "2025-01-13",
        type: "expense" as const,
      },
      {
        id: 4,
        description: "Salary Deposit",
        amount: 3200.0,
        category: "Income",
        date: "2025-01-01",
        type: "income" as const,
      },
    ],
  });

  useEffect(() => {
    supabase.auth.getSession().then(({ data }) => {
      if (!data.session) {
        router.push("/login");
      } else {
        setIsLoading(false);
        // Fetch user name from Supabase
        supabase.auth.getUser().then(({ data: { user } }) => {
          setUserName(
            user?.user_metadata?.name || user?.email?.split("@")[0] || "User"
          );
        });
      }
    });
    // Optionally, subscribe to auth changes for real-time updates
    const { data: listener } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        if (!session) {
          router.push("/login");
        }
      }
    );
    return () => {
      listener?.subscription.unsubscribe();
    };
  }, [router]);

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("en-PH", {
      style: "currency",
      currency: "PHP",
      minimumFractionDigits: 2,
    }).format(amount);
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center text-xl">
        Loading...
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <Navigation />
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header: Welcome + CTAs */}
        <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6 gap-4">
          <div>
            <h1 className="text-3xl font-bold text-slate-900 mb-2">
              Welcome back, {userName}!
            </h1>
            <p className="text-slate-600">
              {`As a ${user.occupation}, you're on track to achieve your ${user.financialGoal} goals. Here's your personalized financial overview for January 2025.`}
            </p>
          </div>
          <div className="flex gap-3">
            {/* CTA Buttons from InteractiveDashboard */}
            <Button
              className="accent-primary hover:accent-primary text-white"
              onClick={() => setShowUploadModal(true)}
            >
              <svg
                className="mr-2 h-4 w-4"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M4 16v2a2 2 0 002 2h12a2 2 0 002-2v-2M7 10l5 5m0 0l5-5m-5 5V4"
                />
              </svg>
              Scan Receipt
            </Button>
            <Button variant="outline">
              <svg
                className="mr-2 h-4 w-4"
                fill="none"
                stroke="currentColor"
                strokeWidth="2"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M4 4v16h16V4H4zm4 4h8v8H8V8z"
                />
              </svg>
              Export Data
            </Button>
          </div>
        </div>
        {/* Modal for uploading receipt */}
        {showUploadModal && (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-40">
            <div className="bg-white rounded-lg shadow-lg p-6 w-full max-w-sm mx-2 relative">
              <button
                className="absolute top-2 right-2 text-slate-500 hover:text-slate-900 text-xl"
                onClick={() => setShowUploadModal(false)}
                aria-label="Close"
              >
                &times;
              </button>
              <h2 className="text-lg font-bold mb-4">Upload Receipt Image</h2>
              <input
                ref={fileInputRef}
                type="file"
                accept="image/*"
                className="block w-full mb-4"
                aria-label="Upload receipt image"
                onChange={(e) => {
                  const file = e.target.files && e.target.files[0];
                  if (file) {
                    setUploadedFile(file);
                    setPreviewUrl(URL.createObjectURL(file));
                    setUploadSuccess(true);
                  } else {
                    setUploadedFile(null);
                    setPreviewUrl(null);
                    setUploadSuccess(false);
                  }
                }}
              />
              {previewUrl && (
                <div className="mb-4 w-full flex flex-col items-center">
                  <img
                    src={previewUrl}
                    alt="Receipt Preview"
                    className="max-h-48 rounded border mb-2"
                  />
                  {uploadSuccess && (
                    <span className="text-green-600 font-medium mb-2">
                      File uploaded successfully!
                    </span>
                  )}
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => {
                      setUploadedFile(null);
                      setPreviewUrl(null);
                      setUploadSuccess(false);
                      setOcrText("");
                      if (fileInputRef.current) fileInputRef.current.value = "";
                    }}
                  >
                    Remove Picture
                  </Button>
                  <Button
                    className="mt-2"
                    disabled={ocrLoading}
                    onClick={async () => {
                      if (!uploadedFile) return;
                      setOcrLoading(true);
                      setOcrText("");
                      setOcrError("");
                      try {
                        const { data } = await Tesseract.recognize(
                          uploadedFile,
                          "eng"
                        );
                        setOcrText(data.text);
                        // --- Begin advanced parsing ---
                        const lines = data.text
                          .split("\n")
                          .map((line) => line.trim().replace(/\s+/g, " "))
                          .filter(Boolean);
                        const items: { item: string; amount: number }[] = [];
                        const skipKeywords =
                          /total|cash|vat|change|amount|tendered|balance|invoice|tin|date|time|receipt|ref|no\.|store|customer|card|payment|transaction|tax|sub\s*total|discount|senior|pwd|address|contact|number|qty|unit|price|sales|vatable|non\-vat|vat\s*exempt|vat\s*zero|gross|net|rounding|service|charge|table|order|waiter|served|by|pos|machine|serial|official|business|permit|biller|bill|clerk|cashier|branch|terminal|reg|register|trans|trans#|trans\s*no|or\s*no|bill\s*no|order\s*no|ref\s*no|reference|transaction|approval|auth|code|trace|mid|tid|batch|seq|seq#|card|type|issuer|bank|account|acct|exp|cvv|pin|entry|mode|chip|swipe|tap|contactless|ecr|merchant|loyalty|points|reward|promo|promo|offer|valid|until|expiry|exp|date|time|hour|minute|sec|am|pm|day|month|year|week|weekday|sunday|monday|tuesday|wednesday|thursday|friday|saturday/i;
                        for (const line of lines) {
                          if (
                            !line ||
                            line.length < 3 ||
                            line.length > 50 ||
                            line === line.toUpperCase() ||
                            skipKeywords.test(line)
                          ) {
                            continue;
                          }
                          // Regex: item name, then price (e.g., "Milk 60.00")
                          const match = line.match(
                            /^([a-zA-Z\s\d\-]+?)\s+([\d,.]{1,10})$/
                          );
                          if (match) {
                            const item = match[1].trim();
                            const amount = parseFloat(
                              match[2].replace(/,/g, "")
                            );
                            if (
                              item &&
                              !/^(total|amount|change|cash|vat|tax)$/i.test(
                                item
                              ) &&
                              !isNaN(amount) &&
                              amount > 0 &&
                              amount < 50000
                            ) {
                              items.push({
                                item,
                                amount: Math.round(amount * 100) / 100,
                              });
                            }
                          }
                        }
                        setParsedItems(items);
                        // --- End advanced parsing ---
                        // Also keep the previous simple parsedResult for reference
                        const text = data.text;
                        const amountMatch = text.match(
                          /(?:₱|PHP)\s?([0-9,.]+)/i
                        );
                        const currency = amountMatch
                          ? amountMatch[0].includes("₱")
                            ? "₱"
                            : "PHP"
                          : undefined;
                        const amount = amountMatch ? amountMatch[1] : undefined;
                        let item = undefined;
                        for (const line of lines) {
                          if (!amountMatch || !line.includes(amountMatch[0])) {
                            if (
                              !item &&
                              line.length > 2 &&
                              !/^(total|amount|change|cash|vat|tax|date|time|receipt|no\.|ref|invoice|php|₱)/i.test(
                                line
                              )
                            ) {
                              item = line;
                              break;
                            }
                          }
                        }
                        setParsedResult({ amount, currency, item });
                      } catch (err) {
                        setOcrError(
                          "Failed to extract text from image. Please try again or use a clearer image."
                        );
                      } finally {
                        setOcrLoading(false);
                      }
                    }}
                  >
                    {ocrLoading ? "Processing..." : "Run OCR"}
                  </Button>
                  {ocrError && (
                    <div className="mt-4 w-full bg-red-50 rounded p-3 border text-sm text-red-700 max-h-40 overflow-auto">
                      <strong>Error:</strong>
                      <pre className="whitespace-pre-wrap break-words">
                        {ocrError}
                      </pre>
                    </div>
                  )}
                  {ocrText && (
                    <div className="mt-4 w-full bg-slate-50 rounded p-3 border text-sm text-slate-700 max-h-40 overflow-auto">
                      <strong>Extracted Text:</strong>
                      <pre className="whitespace-pre-wrap break-words">
                        {ocrText}
                      </pre>
                    </div>
                  )}
                  {parsedResult && (
                    <div className="mt-4 w-full bg-green-50 rounded p-3 border text-sm text-green-700">
                      <strong>Parsed Result:</strong>
                      <div>
                        Amount:{" "}
                        {parsedResult.amount || (
                          <span className="text-slate-400">Not found</span>
                        )}
                      </div>
                      <div>
                        Currency:{" "}
                        {parsedResult.currency || (
                          <span className="text-slate-400">Not found</span>
                        )}
                      </div>
                      <div>
                        Item:{" "}
                        {parsedResult.item || (
                          <span className="text-slate-400">Not found</span>
                        )}
                      </div>
                    </div>
                  )}
                  {parsedItems && parsedItems.length > 0 && (
                    <div className="mt-4 w-full bg-blue-50 rounded p-3 border text-xs text-blue-700 max-h-40 overflow-auto">
                      <strong>Parsed Items (JSON):</strong>
                      <pre className="whitespace-pre-wrap break-words">
                        {JSON.stringify(parsedItems, null, 2)}
                      </pre>
                    </div>
                  )}
                </div>
              )}
              <Button
                className="w-full"
                onClick={() => setShowUploadModal(false)}
              >
                Done
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
